body, html{
    width: 100%;
    // height: 100%;
}

*{
    margin: 0;
    box-sizing: border-box;
}


//VARS

$accent-color: #13404E;
$side-margin: 5vw;


//UNI

@font-face {
    font-family: moderat;
    src: url(/assets/Moderat-Black.otf);
}

ul{
    list-style-type: none;
    padding: 0;
    margin: 0;
}

a{
    text-decoration: none;
    font-size: 17px;
    font-family: moderat, sans-serif;
    color: inherit;
}

h1, h2, h3, h4, h5, p, button, input{
    font-family: moderat, sans-serif;
}

span{
    font-family: inherit;
}

button{
    background: transparent;
    border: none;
}

.o-block{
    border-radius: 15px;
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100%;
    color: #fff;
    margin: 25px 0;

    &.--intro{
        height: 100vh !important;

        & > a > img, & > img{
            width: 100%;
            transform: translate(0, -25%) !important;
        }
    }

    &.--hero{
        & > a > img, & > img{
            width: 120% !important;
            transform: translate(-8%, -5%) !important;
        }
    }

    &.--full-width{
        height: 52vh;

        & > a > img, & > img{
            width: 100%;
            transform: translate(0, 0);
        }

        .c-block-content{
            &.--center{
                h2{
                    font-size: 2em !important;
                }

                .o-block-author{
                    position: absolute;
                    bottom: 5vh;
                    left: 5vw;
                    z-index: 2;
                }

                .o-block-text{
                    transform: translate(-50%, -80%);
                }

            }
        }

        .o-block-text{
            position: absolute;
            top: 30%;
            left: 3vw;
            transform: translate(0, -50%);
            z-index: 2;
        }

        .o-block-author{
            position: absolute;
            bottom: 5vh;
            left: 5vw;
            z-index: 2;
        }
    }

    & > img, & > a > img{
        width: 100%;
        transform: scale(1.1);
        position: relative;
        z-index: 0;
    }

    &::after{
        // display: none;
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        pointer-events: none;
        background: radial-gradient(94.35% 149.2% at 4.07% 96.36%, rgba(31, 31, 31, 0.55) 0%, rgba(140, 140, 140, 0.281962) 62.41%, rgba(255, 255, 255, 0) 100%);;
    }

    .c-block-content{
        padding: 2vh 2vw;
        width: 100%;
        height: 100%;
        display: flex;
        position: absolute;
        z-index: 3;
        top: 0;
        left: 0;
        flex-direction: column;
        justify-content: space-between;

        &.--small{
            padding: 5vh 5vw;

            .o-block-text{
    
                h2{
                    font-size: 30px;
                    //width: 80%;
                    margin-bottom: 30px;
                }

                p{
                    font-size: 14px;
                    //width: 70%;
                }
            }
        }

        &.--medium{
            padding: 5vh 5vw;

            .o-block-text{
                h2{
                    font-size: 28px;
                    //width: 80%;
                    margin-bottom: 30px;
                }

                p{
                    font-size: 14px;
                    //width: 70%;
                }
            }

            .o-block-author{
                bottom: 10% !important;
                left: 8%;
            }
        }
    
        &.--large{
            padding: 5vh 5vw;

            .o-block-text{
    
                h2{
                    font-size: 30px;
                    //width: 80%;
                    margin-bottom: 20px;
                }
    
                p{
                    font-size: 20px;
                    //width: 70%;
                }
            }
        }

        &.--center{
            .o-block-text{
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                z-index: 2;
                width: 80%;

                h2{
                    text-align: center;
                    font-size: 26px;
                }

                p{
                    display: none;
                }
            }
        }

    }

    .o-block-text{
        z-index: 2;
        color: #fff;

        h2, p{
            text-align: center;
        }
        
    }

    .o-block-author{
        display: flex;
        align-items: center;
        z-index: 2;

        p{
            margin-right: 12px;
        }

        img{
            width: 50px;
        }
    }

    .o-btn{
        position: absolute;
        left: 50%;
        transform: translate(-50%, 0);
        bottom: 12%;
        z-index: 2;
    }

    .o-cover{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #fff;
        pointer-events: none;
        transform: translate(0, 0);
        z-index: 4;
    }


    p, h1, h2, h3, a{
        color: #fff;
    }
}


.line-parent{
    overflow: hidden;
    display: block;
    text-align: inherit;
}

.line-child{
    display: block;
    text-align: inherit;
    transform-origin: center bottom;
    transform-style: preserve-3d;
    //transform: translate(0, 100%);
    //transition: .4s;
}

.o-sec-title{
    font-size: 30px;
    margin-bottom: 35px;
    color: rgb(22, 22, 22) !important;

    &.--center{
        text-align: center;
    }
}

.o-section{
    margin: 0 0 14vh 0;
}

.c-btn{
    padding: 35px 40px;
    background-color: #267F69;
    border-radius: 8px;
    display: block;
    text-align: center;
    width: 50%;
    font-size: 18px;
    color: #fff;

    &.--center{
        width: 70%; 
        margin: 6vh auto -10vh auto;
    }
}

.o-flex{
    display: block;

    // &.--two, &.--three{
    //     & > div{
    //         flex: 100%;
    //     }
    // }
}

.c-full-title{
    margin-top: 12vh;
    text-align: center;

    h2{
        font-size: 25px;
        margin-bottom: 8vh;
    }

    a{
        width: 70%;
        margin: auto;
    }
}


.c-cookies{
    position: fixed;
    bottom: 8vh;
    left: 5vw;
    width: max-content;
    padding: 2vh 3vw;
    background-color: #fff;
    color: #000;
    border: 3px solid #000;
    border-radius: 12px;
    z-index: 11;

    p{
        display: inline-block;
    }

    a{
        display: inline-block;
        margin-left: 40px;
        padding: 10px 25px;
