//NAV

.c-nav{
    padding: 4vh $side-margin 4vh $side-margin;
    position: fixed;
    width: 100%;
    background: #fff;
    z-index: 10;

    .c-type-1{
        display: none;
        // display: none;
        width: 100%;

        .c-mobile{
            display: none;
            align-items: center;
            justify-content: space-between;

            a{
                img{
                   width: 170px; 
                }
                
            }

            button{
                display: block;
            }
        }

        .c-dropdown{
            display: none;
        }
    }
    .c-type-2{
        // display: none;
        display: flex;
        align-items: center;
        justify-content: space-between;

        div:nth-child(2){
            img{
                width: 160px;
            }
        }
    }
}

.c-prog{
    height: 6px;
    position: fixed;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    transform: scaleX(0);
    transform-origin: left;
    background-color: $accent-color;
    z-index: 12;

    &.fam{
        background-color: #5D4497;
    }

    &.fit{
        background-color: #FECB00;
    }

    &.health{
        background-color: #1E738B;
    }
}


//MENU BTN

.menu-icon {
    transform: scale(1.5);
}

:root {
	--bar-bg: #212529;
}

.menu-icon {
	position: relative;
	width: 30px;
	height: 30px;
	cursor: pointer;

	.menu-icon__cheeckbox {
		display: block;
		width: 100%;
		height: 100%;
		position: relative;
		cursor: pointer;
		z-index: 2;
		-webkit-touch-callout: none;
		position: absolute;
		opacity: 0;
	}
	div {
		margin: auto;
		position: absolute;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		width: 22px;
		height: 9px;
	}
	span {
		position: absolute;
		display: block;
		width: 100%;
		height: 2px;
		background-color: var(--bar-bg, #000);
		border-radius: 1px;
		transition: all 0.2s cubic-bezier(0.1, 0.82, 0.76, 0.965);

		&:first-of-type {
			top: 0;
            left: -2px;
		}
		&:last-of-type {
			bottom: 0;
            right: -2px;
		}
	}
	&.active div {
		span {
			&:first-of-type {
				transform: rotate(45deg);
				top: 2px;
                left: 0;
			}
			&:last-of-type {
				transform: rotate(-45deg);
				bottom: 5px;
                right: 0;
			}
		}
	}

	&.active:hover span:first-of-type,
	&.active:hover span:last-of-type,
	&:hover .menu-icon__cheeckbox:checked + div span:first-of-type,
	&:hover .menu-icon__cheeckbox:checked + div span:last-of-type {
		width: 22px;
	}

	&:hover {
		// no need hover effect on mobile.
		@media (min-width: 1024px) {
			span:first-of-type {
				width: 26px;
			}

			span:last-of-type {
				width: 12px;
			}
		}
	}
}

// MENU END


.c-mobile-menu{
    width: 100%;
    height: 100%;
    overflow-y: auto;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    padding: 15vh $side-margin 2vh $side-margin;
    visibility: hidden;
    z-index: 9;

    &.--visible{
        visibility: visible !important;
    }

    div{
        padding-bottom: 77px;
        
        p{
            color: $accent-color;
            font-size: 30px;
            margin-bottom: 20px;
        }

        a{
            font-size: 18px;
            margin-bottom: 8px;
            display: block;
        }
    }

    .c-box{
        display: flex;
        align-items: center;
        justify-content: space-between;

        img{
            width: 90px;
            border-radius: 8px;
        }
    }
}



.c-fit-menu{
    width: 100%;
    visibility: hidden;
    height: 100%;
    overflow-y: auto;
    position: fixed;
    top: 0;
    z-index: 9;
    left: 0;
    background: #fff;
    padding: 15vh $side-margin;
    border-radius: 8px;
    border: none;
    padding-bottom: 6vh;

    &.--visible{
        visibility: visible !important;
    }

    &.--fam{
        .c-fit-menu_links{
            p{
                color: #5D4497;
            }
        }
    }

    &.--fit{
        .c-fit-menu_links{
            p{
                color: #FECB00;
            }
        }
    }

    &.--health{
        .c-fit-menu_links{
            p{
                color: #1E738B;
            }
        }
    }



    & > div{
        display: block;
    }

    & > div:last-child{
        display: flex;
        align-items: center;
        justify-content: space-between;

        img{
            width: 90px;
        }
    }
}

.c-fit-menu_links{
    padding-bottom: 77px;
    
    p{
        font-size: 30px;
        margin-bottom: 20px;
    }

    a{
        font-size: 18px;
        margin-bottom: 8px;
        display: block;
    }

}



main{
    padding: 22vw $side-margin;

    &.fam{
        .c-btn{
                background-color: #5D4497;
            }
    }

    &.fit{
        .c-btn{
                background-color: #FECB00;
            }
    }

    &.health{
        .c-btn{
                background-color: #1E738B;
            }
    }
}

.c-hero{
    display: block;
}

.second-box{
    .o-block > a > img{
        width: 106%;
    }
}

.side-box-2{
    height: 50vh;
    .o-btns{
        position: absolute;
        width: 100%;
        left: 0;
        padding: 0 20px;
        text-align: center;
        z-index: 2;
