//QUIZ

.c-quiz{
    margin: 10vh 0 0 0;

    .--small{
        .o-block-text{
            display: flex;
            align-items: center;
            text-align: center;
            justify-content: center;
        }
    }
}

.title-search{ 
    div{
        display: flex;
        align-items: baseline;
        justify-content: space-between;
    }
}

.atoz{
    overflow: auto;
    width: 100%;
    &::-webkit-scrollbar {display: none;}
    &::-moz-scrollbar {display: none;}
}

.c-atoz{
    display: inline-flex;
    align-items: center;
    justify-content: flex-start;
    overflow-x: auto;

    &.health{
        li{
            background-color: #1E738B !important;
        }
    }

    li{
        background-color: #267F69;
    }


    ul{
        display: flex;
        li{
            aspect-ratio: 1;
            border-radius: 100px;

            a{
                height: 100%;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                color: #fff;
            }
        }
    }

    &.--small{
