//SIGNUP

.c-signup{
    margin: 15vh 0 0 0;

    .o-flex{
        & > div:first-child{
            h2{
                font-size: 40px;
                text-align: center;

            }

            form{
                width: 100%;
                margin-top: 50px;
                margin-bottom: 50px;
            }

